	module.exports = {"data":{"markdownRemark":{"html":"<p>教育会場から帰ってきたとき、私はすぐに奇妙なことを見つけました。道路には多くの浮きがあり、カラフルなリボンで吊り下げられていました。また、人形、景色非常に。 「Lincoln」ブランドの大型車があり、動作しません。カウント、合計32台。確率に従って計算すると、我々と同じ方向、つまり60台以上の車両に同じ番号を付ける必要があります。道路の長さに応じて計算すると、今日上海で結婚している人々はおそらく驚くほどです。私は助けることはできませんが、異なる感じ。</p>\n<p>正午には、東湖ホテルの卒業生を見て、私は今夜結婚していたと言いました。「今日は何日ですか？なぜ人が多いのですか？ 「研究」に戻ることを決めたカレンダーを見るために戻って来て、今日は太陰暦9月18日、太陽暦22、土曜日です。 3つはダブル、おそらく黄Daojiです。誰もが今日になる予定です。</p>\n<p>現時点では、人々は日付、数字、時間などにもっと注意を払うようです。好ましくは8月18日または9月18日であり、好ましくは8日である。過去には、4番のすべての人に、地元のタクシー運転手が4名の免許証を持っていて、涙を浮かべていました。しかし、今では新しい解決策があります。スコアに4つの「FA」があるため、私は安心しています。このような「信念」や民俗習慣は最近繁栄し、過去はあまり強く見えませんでした。</p>\n<p>おそらく、これは多面的な理由でもあります。あるいは、信念の抽象概念から未知の「信念」の数に至るまで、あるいは急速に変化する社会において、社会や人の心の中の物質主義の位置は、人々は自らの運命を把握していませんが、未知の変化で失われないように、あるいは自らの運命に自信がないことを確実にし、潮流に運命をつけることを望みます。</p>\n<p>良い気分を求めるのは分かります。しかし、これは社会の進展が停滞していることを意味するのだろうか？</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0165.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0165.md","frontmatter":{"title":"10月22日 土曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0165.md absPath of file >>> MarkdownRemark"}}

